<template>
    <section>
      <div class="head">
        <i class="iconfont" @click="get">&#xe64b;</i>
        <span></span>
      </div>
       <!--ID-->
      <div class="ZH">
        :{{project.username}}
      </div>
      <input type="text" placeholder="10"><br>
      <input type="text" placeholder="6">
      <div class="btn">
        <button></button>
      </div>
    <div class="foot">
      <p class="p2">--------</p>
      <p class="p3">,<br>: 30<br>: 90<br>: 360<br>*,3,4(31)</p>
    </div>
    </section>
</template>

<script>
    export default {
        name: "ExchangeMem",
      data(){
          return {
            project:{}
          }
      },
      methods:{
        get(){
          window.history.go(-1)
        }
      },
      created(){
          if (localStorage.getItem('personObj')!==null){
            this.project=JSON.parse(localStorage.getItem('personObj'))
          }
        console.log(this.project)
      }
    }
</script>

<style scoped>
  .head{
    width: 100%;
    height: 0.4rem;
    line-height: 0.4rem;
    background-color: #008de1;
    color: white;
    font-size: 0.2rem;
    text-align: center;
    overflow: hidden;
  }
  .iconfont{
    font-family:"iconfont" !important;
    font-size:0.2rem;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
    float: left
  }
  .ZH{
    line-height: 0.4rem;
    height: 0.4rem;
    width: 100%;
    background-color: gray;
  }
  input{
    width: 100%;
    height: 0.35rem;
  }
  button{
    width: 90%;
    background-color: gray;
    height: 0.4rem;
    border-radius: 0.08rem;
  }
  .btn{
    width: 100%;
    text-align: center;
  }
  .p2{
    width: 100%;
    text-align: center;
    font-size: 0.18rem;
    margin-top: 0.3rem;
    color: gray;
  }
  .p3{
    font-size: 0.13rem;
    color: gray;
    margin-left: 0.5rem;
    margin-top: 0.2rem;
  }

</style>



