
<template>
  <section>
    <div class="register">
      <router-link :to="{name:'site'}"><span class="glyphicon glyphicon-menu-left pull-left"></span></router-link>
      <span class="register-span"></span>
    </div>
    <div class="center">
      <input type="text" placeholder="" class="form-control" v-model="valueName" @input="getName" :class="{nameColor:nameColor}">
      <p v-if="nameColor"></p>
      <input type="text" placeholder="//" class="form-control" v-model="valueSite" @click="getSite" :class="{nameColor:site}">
      <p v-if="site"></p>
      <input type="text" placeholder="" class="form-control" v-model="valueSite1" @input="getSite1" :class="{nameColor:site1}">
      <p v-if="site1">{{site2? '':''}}</p>
      <input type="text" placeholder="" class="form-control" v-model="valueNumber" @input="getNumber"  :class="{nameColor:number}">
      <p v-if="number"></p>
      <input type="text" placeholder="()" class="form-control" v-model="valueNumber1" @input="getNumber1" :class="{nameColor:number1}">
      <p v-if="number1"></p>
    </div>
    <div class="div">
      <button class="btn btn-success" @click="toArray" :disabled="$store.state.text"></button>
    </div>
    <mt-popup v-model="popupVisible" position="center"class="animated bounceIn center-popup" :closeOnClickModal='false' :modal="false">
      <div >
        <i class="iconfont">&#xe604;</i>
        <p class="p">{{content}}</p>
        <button class="btn btn-success" @click="popup"></button>
      </div>
    </mt-popup>
  </section>
</template>

<script>
  import Vue from 'vue'
  import { Popup } from 'mint-ui';
  Vue.component(Popup.name, Popup);
  export default {
    name: "increase",
    data(){
      return {
        valueName: this.$store.state.valueName,//
        valueSite: this.$store.state.site,//
        valueSite1: this.$store.state.valueSite1,//
        valueNumber: this.$store.state.valueNumber,//
        valueNumber1: this.$store.state.valueNumber1,//
        nameColor:'',//p 
        site:'',//p 
        site1:'',//: p
        site2:'',//: p
        number:'',//p 
        Number:'',
        number1:'',//  p 
        popupVisible:false,//
        content:'',//
        personID:JSON.parse(localStorage.getItem("personObj")).id,//id
        personGeohash:this.$store.state.city.geohash,//
      }
    },
    methods:{
      getSite(){
        // 
        if (this.valueSite.length == 0){
          this.site = true
        }else {
          this.site = false
        }
        this.$router.push({name:'searchAddress'})
      },
      getName(){
        // 
        if (this.valueName.length == 0){
          this.nameColor = true
        }else {
          this.nameColor = false
        }
          this.$store.commit('getName',this.valueName)
      },
      getSite1(){
        // 
        if (this.valueSite1.length == 0 ){
          this.site1 = true
          this.site2 = true
        }else if (this.valueSite1.length < 2) {
          this.site1 = true
          this.site2 = false
        }else if (this.valueSite1.length > 2){
          this.site1 = false
        }
        this.$store.commit('getSite1',this.valueSite1)
      },
      getNumber(){
        // 
        if (!(/^1(3|4|5|7|8)\d{9}$/.test(this.valueNumber))) {
          this.number = true
          this.$store.commit('button', false)  //
          console.log(1)
        } else {
          this.number = false
          console.log(2)
        }
        this.$store.commit('getNumber',this.valueNumber)
      },
      getNumber1(){
        // 
        if (this.valueNumber1.length == 0) {
          this.number1 = false
        }else {
          if (!(/^1(3|4|5|7|8)\d{9}$/.test(this.valueNumber1))) {
            this.number1 = true
          } else {
            this.number1 = false
          }
        }
        this.$store.commit('getNumber1',this.valueNumber1)
      },
      toArray(){
        // 
        if (this.valueSite1.length < 2 || this.valueSite.length == 0) {
          this.content =  ''
          this.popupVisible = true
        }
        if (this.valueNumber.length == 0) {
          this.content =  ''
          this.popupVisible = true
        }
        //
        const url = `https://elm.cangdu.org/v1/users/${this.personID}/addresses`;
        this.$http({
          method: 'post',
          url: url,
          withCredentials: true, // false
          data:{
            address: this.valueSite,
            address_detail: this.valueSite1,
            geohash: this.personGeohash,
            name: this.valueName,
            phone: this.valueNumber,
            tag: '',
            sex: 2,
            poi_type:0,
            phone_bk:this.valueNumber1,
            tag_type:2
          }
        }).then(res=>{
          console.log(res.data)
          if (res.data.status == 1) {
            const obj = {name:this.valueName, site:this.valueSite,site1:this.valueSite1, number:this.valueNumber, number1:this.valueNumber1}
            this.$store.commit('obj',obj)
            console.log(obj)
            this.$store.state.valueName = ''
            this.$store.state.valueSite1 = ''
            this.$store.state.valueNumber = ''
            this.$store.state.valueNumber1 = ''
            this.$router.push({name:'site'})
          }
        })

      },
      popup(){
        // 
        this.popupVisible = false
      }
    },
    updated(){
      //
      if (this.valueName.length != 0 && this.valueSite.length != 0 || this.valueSite1.length != 0 ){
        this.$store.commit('button', false)
      }else {
        // this.text = true
        this.$store.commit('button', true)
      }
    },
    created(){
      console.log(this.personID)
    }
  }
</script>

<style scoped>
  .center-popup{
    position: absolute;
    top: 30%;
    left: 0.25rem;
    text-align: center;
    border-radius: 0.1rem;
    width: 90%;
  }
  .center-popup p{
    font-size: 0.2rem;
    color: #000;
  }
  .center-popup button{
    width: 100%;
    font-size: 0.2rem;
    font-weight: 900;
  }
  .center{
    padding: 0.03rem 0;
    width: 100%;
    margin-top: 0.1rem;
    background-color: white;
  }
  .center input{
    width: 94%;
    margin: 0.1rem auto;
    background-color: #F5F5F5;
  }
  .div{
    width: 100%;
    margin-top: 0.3rem;
    height: 0.4rem;
  }
  .div button{
    width: 96%;
    margin-left: 0.075rem;
    height: 100%;
  }
  p{
    color: red;
    margin-left: .1rem;
    font-size: 0.12rem;
  }
  .nameColor{
    border: 0.01rem solid red;
  }
  .iconfont{
    font-family:"iconfont" !important;
    font-size:1rem;
    font-style:normal;
    color: #F8BB86;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
  }
</style>




