import Vue from 'vue'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App'
import router from './router'
import Vuex from 'vuex'
import iView from 'iview';
import 'iview/dist/styles/iview.css';


import './css/iconfont.css'
Vue.use(Vuex);
Vue.use(MintUI);
Vue.use(ElementUI);
Vue.use(iView);
Vue.config.productionTip = false;
const store = new Vuex.Store({
  state: {
    personMsg:{}, // 
    history:[],//
    value: [],//
    products:'',// 
    site:'',// 
    city:{}, //
    store:{}, // 
    valueName:'',//
    valueSite1:'',//
    valueNumber:'',//
    valueNumber1:'',//
    text:'',//
    cityID:'',//id
    shopCar:[],// ()
    goods:'',// 
    RemarksArr:[],// 
    nameOne:'',
    address:{},// 
    arr1:'',//
  },
  mutations: {
    // 
    personMassage(state, p){
      state.personMsg = p;
      localStorage.setItem("personObj", JSON.stringify(state.personMsg));
    },
    // 
    // history(state, payload) {
    //   let com =  payload;
    //   let list = JSON.parse(localStorage.getItem("arr") || '[]');
    //   list.unshift(com);
    //   let set = new Set(list);
    //   localStorage.setItem("arr", JSON.stringify(set));
    // },
    // 
    search(state, v){
      state.value.push(v);
      let set = new Set(state.value);
      localStorage.setItem("values", JSON.stringify(set));
    },
    // 
    store(state, v){
      state.store = v;
      localStorage.setItem("storeObj", JSON.stringify(state.store));
    },
    event1(state, site){
      state.site = site;
    },
    getName(state,name){
      state.valueName = name;
    },
    getSite1(state,site1){
      state.valueSite1 = site1;
    },
    getNumber(state,number){
      state.valueNumber = number;
    },
    getNumber1(state,number1){
      state.valueNumber1 = number1;
    },
    button(state, i){
      state.text = i
    },
  },

});

new Vue({
  el: '#app',
  render: h => h(App),
  store,
  router,
  components: { App },
  template: '<App/>'
});




