<template>
  <section>
    <div class="register">
      <router-link :to="{name:'account'}"><span class="glyphicon glyphicon-menu-left pull-left"></span></router-link>
      <span class="register-span"></span>
    </div>
    <div class="centre">
      <!--,-->
      <input type="text" placeholder="" v-model="valueParser" @input="judge" :class='{color:text}'>
      <p :style="{color:text ? colorRed: color}">{{text ?  '524': '(5-24)'}}</p>
      <button class="btn btn-primary" :disabled="text"  @click="toAccount"></button>
    </div>
  </section>
</template>

<script>
    export default {
        name: "alter",
      data(){
          return {
            valueParser:'',// 
            text:'',
            color: '#767676',
            colorRed:'red',
            personMsg:JSON.parse(localStorage.getItem('personObj')),//
          }
      },
      methods:{
        judge(){
          //,
          if (this.valueParser.length < 5){
            this.text = true
          }else {
            this.text = false
          }
        },
        toAccount(){
          //Vuexaccount
          this.$router.push({name:'account'})
          //
          this.personMsg.username = this.valueParser
          this.$store.commit('personMassage',this.personMsg)
        }
      }
    }
</script>

<style scoped>
  .centre {
    width: 96%;
    margin:auto;
  }
  .centre input{
    width: 100%;
    margin-top: 0.1rem;
    background-color: #F2F2F2;
  }
  .color{
    border: 0.01rem solid red;
  }
  .centre p{
    width: 100%;
    margin-top: 0.1rem;
    font-size: 0.12rem;
  }
  .centre button{
    width: 100%;
    height: 0.4rem;
    margin-top: 0.1rem;
    background-color: #3199E8;
    transition: all 1s;
  }
</style>



